@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;700&display=swap");
:root {
  --main-white: #f0f0f0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body {
  height: 100vh;
  width: 100%;
  font-family: "Poppins", sans-serif;
  background-color: #44557b;
  overflow-x: hidden;
}
nav {
  background-color: #c32f43;
  color: #ffffff;
  height: 9vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: 400;
  gap: 0 5%;
  font-size: 1.4rem;
  position: fixed;
  top: 0;
  a {
    color: var(--main-white);
    text-decoration: none;
  }
  a:nth-child(3) {
    margin-right: 2em;
  }
}
#welcome-section {
  background-color: #2c2d30;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h1.title {
    color: var(--main-white);
    font-weight: 700;
    font-size: 3rem;
    padding: 0.1em;
  }
  p {
    color: #c32f43;
    font-size: 1.8rem;
    font-weight: 200;
  }
}
#projects-section {
  color: var(--main-white);
  text-align: center;
  h2 {
    font-size: 2rem;
    text-align: center;
    max-width: 23em;
  }
  .projects-container {
    display: grid;
    justify-items: center;
    grid-template-columns: 1fr;
    align-items: center;
    gap: 1em;
    margin: 2em 0;
  }
  .project-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 27em;
    .project-img {
      height: 15em;
      width: 100%;
      object-fit: cover;
    }
    p {
      padding: 1em 2em;
      background-color: #2f3841;
      width: 100%;
      height: 4em;
    }
  }
  #submit-btn {
    background-color: #2f3841;
    color: var(--main-white);
    padding: 1em 2em;
    border: none;
    font-size: 1.2rem;
    font-weight: 400;
    margin-bottom: 2em;
  }
}
#contact-section {
  background-color: #303841;
  color: var(--main-white);
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h2 {
    font-size: 4rem;
    font-weight: 400;
    padding-top: 1em;
  }
  .fa-icons-container {
    margin-top: 2em;
    gap: 1em 2em;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: repeat(3, 1fr);
  }

  padding-bottom: 5em;
}
footer {
  hr.red-line {
    background-color: #c32f43;
    height: 0.5em;
    border: none;
  }
  background-color: #2c2d30;
  color: var(--main-white);
  .container {
    padding: 5em 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  p {
    max-width: 10em;
  }
}
@media only screen and (min-width: 650px) {
  #projects-section {
    h2 {
      text-align: center;
      max-width: 10em;
      margin: 0 auto;
    }
    .projects-container {
      display: grid;
      justify-items: center;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, 1fr);
      padding: 0;
    }
  }
}
@media only screen and (min-width: 956px) {
  #projects-section {
    h2 {
      text-align: center;
      max-width: 16em;
      margin: 0 auto;
    }
    .projects-container {
      display: grid;
      justify-items: center;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      gap: 1em;
    }
  }
}
